import React, {memo, useCallback, useMemo} from 'react';
import {WebView as RNWebView} from 'react-native-webview';
import {ActivityIndicator, Alert, View} from 'react-native';
import {getBottomSpace, getStatusBarHeight} from 'react-native-iphone-x-helper';
import { useNavigationParams } from '@true/core';
import { Core, Fetch } from '@true/core';
import { goBack } from '@true/core';
import { HeaderClose } from "@/components/HeaderClose";

export interface BaseDriveFile {
  ext: string;
  fid: string;
  from_base_drive: 0 | 1;
  is_image: 0 | 1;
  metatype: 'base';
  name: string;
  preview: string; // url
  refid: string; //number
  size: number;
  src: string;
}

const LoadingIndicator = (
  <ActivityIndicator
    size="large"
    style={{
      position: 'absolute',
      zIndex: 1,
      top: 16 + getStatusBarHeight(true),
      right: 16,
    }}
  />
);

const injectedJavascript = `(function() {
  parent = window;
  window.postMessage = function(data) {
    window.ReactNativeWebView.postMessage(JSON.stringify(data));
  };
})()`;

const renderLoading = () => LoadingIndicator;

export interface BaseDriveScreenParams {
  multi?: number;
  onFiles?: (files: BaseDriveFile[]) => any;
}

export const BaseDriveScreen = memo(function BaseDriveScreen() {
  const {multi = 1, onFiles,} = useNavigationParams<BaseDriveScreenParams>()

  const source = useMemo(() => {
    const baseToken =
      '_uuid' +
      Math.random()
        .toString(32)
        .substring(2);
    const params = {
      access_token: Core.accessToken,
      __code: 'native',
      multi: multi,
      ext: 1,
      base_token: baseToken,
    };

    const extParams = Object.keys(params)
      // @ts-ignore
      .map(key => `${key}=${params[key]}`)
      .join('&');

    const uri = Fetch.__getRealUrl(
      `@drive/picker?${extParams}&base_domain=https%3A%2F%2Fmessage.${Core.sysUrl}`,
    );

    return {uri};
  }, [multi]);

  const onMessage = useCallback((event:any) => {
    try {
      const data = JSON.parse(event.nativeEvent.data);
      const files = data.files as BaseDriveFile[];
      if (files?.length) {
        onFiles?.(files);
        goBack()
      }

    } catch (e:any) {
      Alert.alert(e.toString(), "")
    }
  }, [onFiles]);

  return (
    <View style={{flex:1}}>
      <HeaderClose title={'Base Drive'}/>
      <RNWebView
        source={source}
        onMessage={onMessage}
        renderLoading={renderLoading}
        domStorageEnabled={true}
        allowsInlineMediaPlayback={true}
        sharedCookiesEnabled={false}
        thirdPartyCookiesEnabled={false}
        startInLoadingState={true}
        injectedJavaScriptBeforeContentLoaded={injectedJavascript}
      />
      <View style={{width: '100%', height: getBottomSpace()}}/>
    </View>
  );
});

export default BaseDriveScreen;
