import React, {memo} from 'react';
import {addLangResources, AppInfo, useNavigationParams} from '@true/core';
import {MyMiniApp} from '@true/ui';
import {persistor} from '@/store';
import {InsideRoutes} from '@/InsideRoutes';
import {PostImageProvider} from '@/components/PostThumbnail/postImageProvider';

/* -- setup app info -- */
AppInfo.logo = require('@/assets/images/ic_calendar_event.png');
AppInfo.preloadImage = require('@/assets/images/ic_calendar_event.png');
AppInfo.buttonTextColor = '#ffffff';
AppInfo.title = 'Inside';
AppInfo.subTitle = 'A social network to connect and engage across the company';
AppInfo.primaryColor = '#3AA394';
AppInfo.preSyncData = async () => {};

/* -- add languages -- */
addLangResources({
  en: {...require('@/assets/languages/en.json')},
  vi: {...require('@/assets/languages/vi.json')},
}).then();

const MiniInsideApp = memo(() => {
  const props = useNavigationParams<any>();
  return (
    <PostImageProvider>
      <MyMiniApp
        component={InsideRoutes}
        persistor={persistor}
        onBeforeLift={props?.callback}
      />
    </PostImageProvider>
  );
});

export default MiniInsideApp;
