import {Fonts, styled} from '@true/core';
import {memo} from 'react';
import {IC_OUTLINED_BACKWARD, IC_OUTLINED_FORWARD} from '@true/asset';
import {UIText} from '@true/ui';

interface ItemMenuProps {
  icon: any;
  title: string;
  onPress: () => void;
}

export const ItemMenu = memo(function (props: ItemMenuProps) {
  const {icon, title, onPress} = props;
  return (
    <SContainer onPress={onPress}>
      <SImage source={icon} />
      <SecondSection>
        <STextItem>{title}</STextItem>
        <SArrowImage source={IC_OUTLINED_FORWARD} />
      </SecondSection>
    </SContainer>
  );
});
const SContainer = styled.TouchableOpacity`
  flex-direction: row;
  padding: 4px 16px;
  height: 48px;
  align-items: center;
`;

const STextItem = styled(UIText.BodyMedium400)`
  color: ${p => p.theme.neutral1};
`;

const SecondSection = styled.View`
  flex: 1;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
`;
const SImage = styled.Image`
  width: 24px;
  height: 24px;
  margin-right: 12px;
`;
const SArrowImage = styled.Image`
  width: 24px;
  height: 24px;
  tint-color: ${p => p.theme.neutral3};
`;
