import {Avatar, ScreenWrapper, UIText} from '@true/ui';
import {memo, useCallback, useMemo} from 'react';
import {Fonts, logout, styled, useMe} from '@true/core';
import {HeaderMenu} from '@/screens/MenuScreen/HeaderMenu';
import {Image, ScrollView, View} from 'react-native';
import {globalStyles} from '@/utils/globalStyle';
import {ItemMenu} from '@/screens/MenuScreen/ItemMenu';
import {
  IC_FILLED_APARTMENT,
  IC_FILLED_BOOKMARK,
  IC_FILLED_CEO_LETTER,
  IC_FILLED_CHECK_CIRCLE,
  Icons
} from '@true/asset';
import {
  navigateToBoardLetterScreen,
  navigateToCoreValueScreen,
  navigateToProfileScreen,
  navigateToSettingScreen,
  navigateToVisionScreen,
} from '@/utils';
import {EUserRole, FlexCenter, FlexSpaceBetween} from '@/utils/helper';
import {useSetting} from '@/store/settings';
import {Svgs} from '@/assets';
import {SvgXml} from 'react-native-svg';

export const MenuScreen = memo(function () {
  const me = useMe();
  const setting = useSetting();
  const onNavigateToProfile = useCallback(() => {
    navigateToProfileScreen({username: me?.username || '', user_id: me?.id});
  }, []);

  const isAdmin = useMemo(() => {
    return me?.role && Number(me?.role) === EUserRole.ADMIN;
  }, []);

  return (
    <ScreenWrapper isGrey={true}>
      <SContainer>
        <HeaderMenu />
        <ScrollView bounces={false}>
          <SProfileSection onPress={onNavigateToProfile}>
            <SRow>
              <FlexCenter>
                <Avatar size={40} userKey={me?.id || '0'} disableClick={true} />
                <View style={globalStyles.ml12}>
                  <STextName>{me?.name}</STextName>
                  <STextGrey>{me?.email}</STextGrey>
                </View>
              </FlexCenter>
              {isAdmin ? (
                <SIcon onPress={navigateToSettingScreen}>
                  <Image source={Icons.Filled.COG_6_TOOTH} />
                </SIcon>
              ) : null}
            </SRow>
          </SProfileSection>
          <SWhiteBackground>
            <FlexSpaceBetween style={globalStyles.mr16}>
              <STextSection>Company</STextSection>
              <FlexCenter>
                <SvgXml xml={Svgs.logo} width={20} height={20} />
                <STextCompanyName>
                  {setting?.sysinfo.name || ''}
                </STextCompanyName>
              </FlexCenter>
            </FlexSpaceBetween>
            <ItemMenu
              icon={IC_FILLED_APARTMENT}
              title={'Visions & missions'}
              onPress={navigateToVisionScreen}
            />
            <ItemMenu
              icon={Icons.Filled.SPARKLES}
              title={'Core values'}
              onPress={navigateToCoreValueScreen}
            />
            <ItemMenu
              icon={IC_FILLED_CEO_LETTER}
              title={'CEO Letter'}
              onPress={navigateToBoardLetterScreen}
            />
            <ItemMenu
              icon={Icons.Filled.USERS}
              title={'Members'}
              onPress={() => {
                console.log('not API yet');
              }}
            />
          </SWhiteBackground>
          <SWhiteBackground>
            <STextSection>Personal</STextSection>
            <ItemMenu
              icon={IC_FILLED_BOOKMARK}
              title={'Saved items'}
              onPress={onNavigateToProfile}
            />
            <ItemMenu
              icon={Icons.Filled.CALENDAR}
              title={'Scheduled items'}
              onPress={onNavigateToProfile}
            />
            <ItemMenu
              icon={IC_FILLED_CHECK_CIRCLE}
              title={'Approvals'}
              onPress={onNavigateToProfile}
            />
          </SWhiteBackground>
          <SLogoutBox onPress={logout}>
            <SIcon>
              <SLogoutImage source={Icons.Outlined.ARROW_RIGHT_ON_RECTANGLE} />
            </SIcon>
            <STextLogout>Log out</STextLogout>
          </SLogoutBox>
        </ScrollView>
      </SContainer>
    </ScreenWrapper>
  );
});

const SContainer = styled.View`
  flex: 1;
`;
const SProfileSection = styled.TouchableOpacity`
  background-color: white;
  padding: 12px 16px;
  border-bottom-width: 1px;
  border-bottom-color: ${p => p.theme.neutral4};
`;
const SWhiteBackground = styled.View`
  background-color: white;
  padding-top: 12px;
  margin-top: 8px;
`;
const SIcon = styled.TouchableOpacity`
  width: 24px;
  height: 24px;
  align-items: center;
  justify-content: center;
`;
const SRow = styled.View`
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
`;
const STextName = styled(UIText.HeaderMedium)`
  color: ${p => p.theme.neutral1};
`;
const STextGrey = styled(UIText.CaptionMedium400)`
  color: ${p => p.theme.neutral2}}
`;
const STextSection = styled.Text`
  font-family: ${Fonts.Regular};
  color: ${p => p.theme.neutral1};
  font-size: 16px;
  font-weight: 500;
  line-height: 24px; /* 150% */
  margin-left: 16px;
`;
const SLogoutImage = styled.Image`
  width: 24px;
  height: 24px;
  tint-color: #cf1322;
`;
const SLogoutBox = styled.TouchableOpacity`
  padding: 16px;
  flex-direction: row;
  background-color: white;
  margin-top: 8px;
`;
const STextLogout = styled.Text`
  color: #cf1322;
  font-family: ${Fonts.Regular};
  font-size: 16px;
  font-weight: 400;
  line-height: 24px; /* 150% */
  margin-left: 8px;
`;
const SLogoIcon = styled.Image`
  width: 20px;
  height: 20px;
`;
const STextCompanyName = styled(UIText.CaptionMedium500)`
  color: ${p => p.theme.neutral2};
  margin-left: 8px;
`;
