import React, {memo, useCallback} from 'react';
import {ScreenWrapper, UIFlashList} from '@true/ui';
import {EOrderUpdate, ETypeUpdate} from '@/store/updates/type';
import {styled, useAsyncFn, useNavigationParams} from '@true/core';
import {CustomHeader} from '@/components/CustomHeader';
import {typeUpdates} from '@/screens/UpdateTypesScreen/typeUpdates';
import {useUpdatesByQuery} from '@/store/updates';
import {PostThumbnail} from '@/components/PostThumbnail';
import {UpdateSkeleton} from '@/components/SkeletonPlaceholder/UpdateSkeleton';
import {
  requestLoadMoreUpdates,
  requestUpdatePosts,
} from '@/store/updates/function';
import {sleep} from '@/utils/helper';
import {useTokenUpdate} from '@/zustand/TokenUpdate';
import {EmptyView} from '@/components/EmptyView';
import {globalStyles} from '@/utils/globalStyle';
import {
  navigateToCreateCoreValueScreen,
  navigateToCreateEvent,
  navigateToCreateLetterScreen,
  navigateToCreatePollScreen,
  navigateToCreatePostScreen,
  navigateToCreateTopicScreen,
} from '@/utils';

export interface FilteredTypeScreenParams {
  type: ETypeUpdate;
}

export const FilteredTypeScreen = memo(function FilteredTypeScrenn() {
  const {type} = useNavigationParams<FilteredTypeScreenParams>();
  const listUpdatesFiltered = useUpdatesByQuery(type);
  const ptoken = useTokenUpdate();

  const onCreate = useCallback(() => {
    switch (type) {
      case ETypeUpdate.UPDATE:
      case ETypeUpdate.STYLED:
      case ETypeUpdate.WELCOME:
      case ETypeUpdate.IDEA:
      case ETypeUpdate.ANNOUNCEMENT:
        return navigateToCreatePostScreen({
          type: type,
        });
      case ETypeUpdate.EVENT:
        return navigateToCreateEvent({
          id: -1,
        });
      case ETypeUpdate.POLL:
        return navigateToCreatePollScreen({
          id: -1,
        });
      case ETypeUpdate.TOPIC:
        return navigateToCreateTopicScreen({
          id: -1,
        });
      case ETypeUpdate.COMPANY_LETTER:
        return navigateToCreateLetterScreen({
          id: -1,
        });
      case ETypeUpdate.CORE_VALUE:
        return navigateToCreateCoreValueScreen({
          id: -1,
        });
      default:
        return null;
    }
  }, []);
  const renderItem = useCallback(({item}: any) => {
    return <PostThumbnail isInFeed={true} id={item} />;
  }, []);
  const [{loading}, refreshHome] = useAsyncFn(async () => {
    await requestUpdatePosts({order: EOrderUpdate.UPDATE});
    return;
  }, []);

  const [{loading: loadingPrev, error: errorPrev}, loadMoreMessage] =
    useAsyncFn(async () => {
      await requestLoadMoreUpdates({ptoken});
      await sleep(0.5);
      return [];
    }, [listUpdatesFiltered.length, ptoken]);

  const onEndReached = useCallback(() => {
    if (loadingPrev) return;
    // if (errorPrev) return;
    // if (!messages.length) return;
    loadMoreMessage().then();
  }, [loadingPrev, errorPrev, listUpdatesFiltered]);
  const onLoadData = useCallback(
    async (isRefreshing: boolean, isLoadMore: boolean) => {
      if (isLoadMore) {
        await loadMoreMessage();
      }
    },
    [loadMoreMessage],
  );

  return (
    <ScreenWrapper isGrey>
      <CustomHeader
        title={typeUpdates[type].title}
        isSearch={false}
        onCreate={onCreate}
        isBack
        isDivider
      />
      <SView style={globalStyles.mt8}>
        {loading ? (
          <UpdateSkeleton />
        ) : (
          <UIFlashList
            data={listUpdatesFiltered}
            renderItem={renderItem}
            refreshing={loading}
            onRefresh={refreshHome}
            onEndReachedThreshold={1}
            ListEmptyComponent={<EmptyView />}
            // nestedScrollEnabled={true}
            // estimatedItemSize={600}
            removeClippedSubviews={true}
            onEndReached={onEndReached}
            onLoadData={onLoadData}
            loadingMore={loadingPrev}
            loadMorable
          />
        )}
      </SView>
    </ScreenWrapper>
  );
});
const SView = styled.View`
  flex: 1;
`;
